.PDFEmbed {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

iframe {
  height: 100vh;
  width: 100%;
  border: none;
}

@media (max-width: 934px) {
  .PDFEmbed {
    width: 93%;
    height: auto;
  }

  iframe {
    height: 90vh;
    width: 100%;
  }
}